// components/GlobeView.tsx
"use client";

import React from "react";

export default function GlobeView({
  width = 240,
  height = 210,
  title = "Global View",
}: {
  width?: number;
  height?: number;
  title?: string;
}) {
  const r = Math.min(width, height) * 0.42;
  const cx = width / 2;
  const cy = height / 2;

  return (
    <div className="w-full">
      <div className="text-xs text-slate-500 mb-2">{title}</div>
      <svg width={width} height={height} className="block mx-auto">
        <defs>
          <radialGradient id="glb" cx="50%" cy="45%" r="60%">
            <stop offset="0%" stopColor="#93C5FD" />
            <stop offset="65%" stopColor="#60A5FA" />
            <stop offset="100%" stopColor="#3B82F6" />
          </radialGradient>
        </defs>
        {/* sphere */}
        <circle cx={cx} cy={cy} r={r} fill="url(#glb)" stroke="#1E3A8A" strokeWidth={1} />

        {/* simple lat/long lines */}
        {[ -60, -30, 0, 30, 60 ].map((lat, i) => (
          <ellipse
            key={`lat-${i}`}
            cx={cx}
            cy={cy}
            rx={r * Math.cos((lat * Math.PI) / 180)}
            ry={r * 0.35}
            fill="none"
            stroke="rgba(255,255,255,0.55)"
            strokeWidth={0.7}
          />
        ))}
        {[ -90, -45, 0, 45, 90 ].map((lng, i) => (
          <path
            key={`lng-${i}`}
            d={describeMeridian(cx, cy, r, (lng * Math.PI) / 180)}
            fill="none"
            stroke="rgba(255,255,255,0.45)"
            strokeWidth={0.7}
          />
        ))}
      </svg>
    </div>
  );
}

// simple meridian path approximation (cosine flattening)
function describeMeridian(cx: number, cy: number, r: number, angle: number) {
  const steps = 40;
  const pts: string[] = [];
  for (let i = 0; i <= steps; i++) {
    const t = -Math.PI / 2 + (i / steps) * Math.PI; // -90..+90 latitude
    const x = cx + r * Math.cos(t) * Math.cos(angle);
    const y = cy + r * Math.sin(t);
    pts.push(`${i === 0 ? "M" : "L"} ${x.toFixed(2)} ${y.toFixed(2)}`);
  }
  return pts.join(" ");
}
